.free-training {
  background: $color-biscay;

  h2 {
    margin-bottom: 41px;
  }

  &__inputs {
    display: grid;
    grid-template-columns: 1fr 1fr;
    max-width: 441px;
    margin-bottom: 22px;
  }

  input {
    box-sizing: border-box;
    background: none;
    border: none;
    border-bottom: solid 1px $connection-border-color;
    max-width: 189px;
    padding: 2px 10px 5px 0;
  }

  input::placeholder {
    font-family: $ff-roboto-condensed;
    font-size: 14px;
    line-height: 16px;
    font-weight: 400;
    color: $color-default-white;
  }

  button {
    box-sizing: border-box;
    display: inline-block;
    vertical-align: baseline;
    font-family: $ff-roboto-condensed;
    font-size: 14px;
    line-height: 16px;
    font-weight: 700;
    text-transform: uppercase;
    text-align: center;
    color: $color-default-white;
    letter-spacing: 0.7px;
    box-shadow: 0 0 0 3px $color-default-white;
    max-width: 407px;
    width: 100%;
    padding-top: 16px;
    padding-bottom: 17px;
    margin-left: 1px;
  }
}
